.pagination a{
    text-align: center;
}
.pagination a{
    color: black;;
    text-decoration: none;
    padding: 8px 15px;
    display: inline-block;
}

.pagination a:hover:not(.active){
    background-color: #FF6B08;
    border-radius: 5px;
    color: #fff;
    font-weight: bold;
    border-radius: 5px;
}

